body {
    width: 100%;
    height: 100%;
    background-color: black;
    margin: 0px;
    padding: 0;
    overflow: hidden;
    font-family: 'Poppins', sans-serif;
}



.fillButton {
    position: relative;
    height: 45px;
    width: 150px;
    margin: 10px 7px;
    padding: 5px 5px;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: 2px;
    color: #ffffff;
    border: 2px #ffffff solid;
    border-radius: 4px;
    outline: 0;
    overflow:hidden;
    background: none;
    z-index: 1;
    cursor: pointer;
    transition:         0.08s ease-in;
    -o-transition:      0.08s ease-in;
    -ms-transition:     0.08s ease-in;
    -moz-transition:    0.08s ease-in;
    -webkit-transition: 0.08s ease-in;
  }



.fill:hover {
    color: rgb(255, 255, 255);
  }



.fill:before {
    content: "";
    position: absolute;
    background: -webkit-gradient(linear, left top, left bottom, from(#72f), to(#c1b));
    background: linear-gradient(#72f, #c1b);
    bottom: 0;
    left: 0;
    right: 0;
    top: 100%;
    z-index: -1;
    -webkit-transition: top 0.09s ease-in;
  }



.fill:hover:before {
    top: 0;
  }



